.taskContainer{
    display: flex;
    flex-direction: row;
    margin-top: 7%;
    width: 95%;
    justify-content: space-between;
    overflow: hidden;
    border-left: 4px solid #2A9D8F;
    padding: 4px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
    z-index: 2;
}

.taskDescription{
    display: flex;
    flex-direction: column;
    width: 80%;
    padding-left: 8px;
}

.taskDescription span{
    position: absolute;
    margin-top: -1px;
    margin-left: -0.5px;
    color: #EDEDED;
    font-size: 20px;
    visibility: hidden;
    pointer-events: none;
}

.active{
    border: 2px solid #989898;
    background-color: #989898;
}

.active + span{
    visibility: visible;
}

.taskContainer h3{
    font-family: Poppins;
    font-weight: 700;
    font-size: 14px;
}

.taskContainer p {
    font-family: Roboto;
    font-size: 12px;
}

.descContainer{
    height: auto;
}

.taskContainer button{
    background: none;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all .15s ease-in;
}

.taskContainer button:hover{
    color: #2A9D8F;
    transform: rotate(-90deg);
}

.taskContainer button span{
    font-size: 20px;
    pointer-events: none;
}