header {
  width: 376px;
  height: 105px;
  background-image: URL("../img/backgroundHeader.svg");
  background-repeat: no-repeat;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  z-index: 0;
}

header a {
  margin-top: 3%;
  margin-left: 3%;
}

header button {
  color: #2a9d8f;
  outline: none;
  border: none;
  background-color: none;
  margin-top: 3%;
  cursor: pointer;
}

.navBtn {
  height: 30px;
}

.userBtn {
  height: 30px;
  margin-right: 3%;
  border: none;
  background-color: none;
}

.userBtn span {
  font-size: 40px;
}
