.app {
  width: 376px;  
  height: auto;
  margin: auto;
  margin-top: 5vh;
  background-color: #fff;
  box-shadow: 8px 8px 24px 0px rgba(0,0,0,0.15);
  overflow: hidden;
}


.container{
  width: 90%;
  margin: auto;
  overflow: hidden;
}

.title1{
  font-family: Montserrat;
  font-size: 22px;
  color: #505050;
  width: 90%;
  margin: auto;
}

.title2{
  font-family: Poppins;
  font-size: 14px;
  font-weight: 400;
  width: 90%;
  margin: auto;
  padding-top: 5%;
}

.image{
  background-image: url(./img/havingFun.svg);
  width: 600px;
  height: 385px;
  position: relative;
  margin-left: -28%;
  margin-top: 50px;
  margin-bottom: -485px;
  z-index: 0;
}

@media screen and (max-width: 600px){
  .app{
    margin-top: 0;
  }
}