@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #537e6c;
  height: 812px;
  border-radius: 50px;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
  border-radius: 50px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #b4b4b4;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

header {
  width: 376px;
  height: 105px;
  background-image: URL(/react-todo-tracker/static/media/backgroundHeader.f16d2735.svg);
  background-repeat: no-repeat;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  z-index: 0;
}

header a {
  margin-top: 3%;
  margin-left: 3%;
}

header button {
  color: #2a9d8f;
  outline: none;
  border: none;
  background-color: none;
  margin-top: 3%;
  cursor: pointer;
}

.navBtn {
  height: 30px;
}

.userBtn {
  height: 30px;
  margin-right: 3%;
  border: none;
  background-color: none;
}

.userBtn span {
  font-size: 40px;
}

.addTaskBackground {
  width: 100%;
  height: 100%;
  background-image: url(/react-todo-tracker/static/media/backgroundAdd.21743471.svg);
  background-repeat: no-repeat;
}

.addContainer {
  width: 90%;
  margin: auto;
  padding-top: 3%;
  padding-bottom: 3%;
  z-index: 3;
}

.addContainer h1 {
  font-family: Montserrat;
  font-size: 22px;
  color: #fff;
}

.addContainer form{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-top: 3%;
}

.addContainer form input{
    height: 25px;
    width: 70%;
    background-color: #2A9D8F;
    border: none;
    outline: none;
    color: #fff;
    border-radius: 5px;
    margin-right: 5%;
    padding-left: 2%;
    font-size: 10px;
    font-family: Poppins;
    font-weight: 200;
}

.addContainer input::-webkit-input-placeholder{
    color: #fff;
}

.addContainer input:-ms-input-placeholder{
    color: #fff;
}

.addContainer input::placeholder{
    color: #fff;
}

.addContainer  button {
  width: 20%;
  height: 25px;
  background-color: #2A9D8F;
  outline: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  transition: all .15s ease-in;
  margin-right: 4%;
}

.addContainer button:hover{
    background-color: #fff;
    color: #000;
    -webkit-transform: translate(-4px,-4px);
            transform: translate(-4px,-4px);
    box-shadow: 6px 6px 0px 1px #2A9D8F;
}

.inputContainer{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.inputContainer input:nth-child(2){
  margin-top: 3%;
}

.taskContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-top: 7%;
    width: 95%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    overflow: hidden;
    border-left: 4px solid #2A9D8F;
    padding: 4px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
    z-index: 2;
}

.taskDescription{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 80%;
    padding-left: 8px;
}

.taskDescription span{
    position: absolute;
    margin-top: -1px;
    margin-left: -0.5px;
    color: #EDEDED;
    font-size: 20px;
    visibility: hidden;
    pointer-events: none;
}

.active{
    border: 2px solid #989898;
    background-color: #989898;
}

.active + span{
    visibility: visible;
}

.taskContainer h3{
    font-family: Poppins;
    font-weight: 700;
    font-size: 14px;
}

.taskContainer p {
    font-family: Roboto;
    font-size: 12px;
}

.descContainer{
    height: auto;
}

.taskContainer button{
    background: none;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all .15s ease-in;
}

.taskContainer button:hover{
    color: #2A9D8F;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

.taskContainer button span{
    font-size: 20px;
    pointer-events: none;
}
.tasksContainer {
  overflow-y: auto;
  height: 565px;
  z-index: 1;
}


@media screen and (max-width: 400px) {
  .tasksContainer {
    /* height: 60.5vh; */
  }
}


.app {
  width: 376px;  
  height: auto;
  margin: auto;
  margin-top: 5vh;
  background-color: #fff;
  box-shadow: 8px 8px 24px 0px rgba(0,0,0,0.15);
  overflow: hidden;
}


.container{
  width: 90%;
  margin: auto;
  overflow: hidden;
}

.title1{
  font-family: Montserrat;
  font-size: 22px;
  color: #505050;
  width: 90%;
  margin: auto;
}

.title2{
  font-family: Poppins;
  font-size: 14px;
  font-weight: 400;
  width: 90%;
  margin: auto;
  padding-top: 5%;
}

.image{
  background-image: url(/react-todo-tracker/static/media/havingFun.7618a48a.svg);
  width: 600px;
  height: 385px;
  position: relative;
  margin-left: -28%;
  margin-top: 50px;
  margin-bottom: -485px;
  z-index: 0;
}

@media screen and (max-width: 600px){
  .app{
    margin-top: 0;
  }
}
