.addTaskBackground {
  width: 100%;
  height: 100%;
  background-image: url("../img/backgroundAdd.svg");
  background-repeat: no-repeat;
}

.addContainer {
  width: 90%;
  margin: auto;
  padding-top: 3%;
  padding-bottom: 3%;
  z-index: 3;
}

.addContainer h1 {
  font-family: Montserrat;
  font-size: 22px;
  color: #fff;
}

.addContainer form{
    display: flex;
    flex-direction: row;
    margin-top: 3%;
}

.addContainer form input{
    height: 25px;
    width: 70%;
    background-color: #2A9D8F;
    border: none;
    outline: none;
    color: #fff;
    border-radius: 5px;
    margin-right: 5%;
    padding-left: 2%;
    font-size: 10px;
    font-family: Poppins;
    font-weight: 200;
}

.addContainer input::placeholder{
    color: #fff;
}

.addContainer  button {
  width: 20%;
  height: 25px;
  background-color: #2A9D8F;
  outline: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  transition: all .15s ease-in;
  margin-right: 4%;
}

.addContainer button:hover{
    background-color: #fff;
    color: #000;
    transform: translate(-4px,-4px);
    box-shadow: 6px 6px 0px 1px #2A9D8F;
}

.inputContainer{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-between;
}

.inputContainer input:nth-child(2){
  margin-top: 3%;
}
